import React, { useEffect, useState } from 'react';
import { Box, Button, Card, CardContent, Grid, makeStyles, TextField } from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import ConsignmentService from 'src/services/consignment';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
}));

const CreateDelivery = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [initialValues, setInitialValues] = useState({
    customerId: '',
    trackingNumber: '',
    SKU: '',
    weight: '',
    chargeableWeight: '',
    pieces: '',
    length: '',
    width: '',
    height: '',
    sendTime: '',
    dispatchTime: '',
    submitTime: '',
    status: '',
    preAssign: '',
    priority: '',
    description: '',
    value: '',
    MLID: '',
    hub: '',
    manifestNumber: '',
    barcode: '',
    matrix: '',
    recipient: '',
    phone: '',
    email: '',
    address1: '',
    address2: '',
    address3: '',
    suburb: '',
    state: '',
    postcode: '',
  });

  const { consignmentId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await ConsignmentService.findConsignmentById(id);

        setInitialValues({
          referenceNumber: result.content.referenceNumber,
          customerId: result.content.customerId,
          trackingNumber: result.content.trackingNumber,
          SKU: result.content.SKU,
          weight: result.content.weight,
          chargeableWeight: result.content.chargeableWeight,
          pieces: result.content.pieces,
          length: result.content.length,
          width: result.content.width,
          height: result.content.height,
          sendTime: result.content.sendTime,
          dispatchTime: result.content.dispatchTime,
          submitTime: result.content.submitTime,
          status: result.content.status,
          preAssign: result.content.preAssign,
          priority: result.content.priority,
          value: result.content.value,
          MLID: result.content.MLID,
          hub: result.content.hub,
          description: result.content.description,
          manifestNumber: result.content.manifestNumber,
          barcode: result.content.barcode,
          matrix: result.content.matrix,
          recipient: result.content.recipient,
          phone: result.content.phone,
          email: result.content.email,
          address1: result.content.address1,
          address2: result.content.address2,
          suburb: result.content.suburb,
          state: result.content.state,
          postcode: result.content.postcode,
        });
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    consignmentId && fetchResults(consignmentId);
  }, [consignmentId, enqueueSnackbar]);

  const navigate = useNavigate();

  const requestWith = async (values, actions) => {
    try {
      await ConsignmentService.createConsignment({
        customerId: values.customerId,
        trackingNumber: values.trackingNumber,
        recipient: values.recipient,
        phone: values.phone,
        email: values.email,
        address1: values.address1,
        address2: values.address2,
        suburb: values.suburb,
        state: values.state,
        postcode: values.state,
      });

      enqueueSnackbar('Create', { variant: 'success' });

      navigate('/app/consignment');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const updateDelivery = async (values, actions, id) => {
    try {
      await ConsignmentService.editConsignment(consignmentId, values);

      enqueueSnackbar('', { variant: 'success' });

      navigate('/app/consignment');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const handleFormSubmit = (values, actions) => {
    !consignmentId ? requestWith(values, actions) : updateDelivery(values, actions, consignmentId);
  };

  const handleCancel = () => {
    navigate(`/app/consignment`);
  };

  const renderContent = () => (
    <Grid container>
      <Grid item container xs={12}>
        {(!consignmentId || (consignmentId && initialValues.customerId)) && (
          <Formik
            initialValues={initialValues}
            validationSchema={Yup.object().shape({
              customerId: Yup.string().max(255).required('*required'),
              trackingNumber: Yup.string().max(255).required('*required'),
              recipient: Yup.string().max(255).required('*required'),
              phone: Yup.string(),
              email: Yup.string().email(),
              address1: Yup.string().max(255).required('*required'),
              address2: Yup.string().max(255),
              suburb: Yup.string().max(255).required('*required'),
              postcode: Yup.string().max(255).required('*required'),
              state: Yup.string().max(255).required('*required'),
            })}
            onSubmit={(values, actions) => {
              handleFormSubmit(values, actions);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit} className={classes.form}>
                {!consignmentId && (
                  <Grid item container xs={12} spacing={2}>
                    <Grid item xs={12}>
                      <h3>Consignment Information</h3>
                    </Grid>
                    <Grid item xs={3}>
                      <TextField
                        error={Boolean(touched.customerId && errors.customerId)}
                        fullWidth
                        helperText={touched.customerId && errors.customerId}
                        label="Client*"
                        margin="normal"
                        name="customerId"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values?.customerId}
                        variant="outlined"
                        disabled={isSubmitting || loading}
                      />
                    </Grid>
                    <Grid item xs={5}>
                      <TextField
                        error={Boolean(touched.trackingNumber && errors.trackingNumber)}
                        fullWidth
                        helperText={touched.trackingNumber && errors.trackingNumber}
                        label="Tracking Number*"
                        margin="normal"
                        name="trackingNumber"
                        onBlur={handleBlur}
                        multiline
                        onChange={handleChange}
                        value={values?.trackingNumber}
                        variant="outlined"
                        disabled={isSubmitting}
                      />
                    </Grid>
                  </Grid>
                )}
                {consignmentId && (
                  <Grid item container xs={12} spacing={2}>
                    <Grid item xs={12}>
                      <h3>Consignment Information</h3>
                    </Grid>
                    {Object.keys(initialValues)
                      .slice(0, 22)
                      .map((key) => (
                        <Grid item xs={key === 'description' ? 12 : 2} key={key}>
                          <TextField
                            error={Boolean(touched[key] && errors[key])}
                            fullWidth
                            helperText={touched[key] && errors[key]}
                            label={key}
                            margin="normal"
                            name={key}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            value={values[key]}
                            variant="outlined"
                            disabled={isSubmitting || loading}
                          />
                        </Grid>
                      ))}
                  </Grid>
                )}
                <Grid item container xs={12} spacing={2}>
                  <Grid item xs={12}>
                    <h3>Recipient Information</h3>
                  </Grid>
                  <Grid item xs={3}>
                    <TextField
                      error={Boolean(touched.recipient && errors.recipient)}
                      fullWidth
                      helperText={touched.recipient && errors.recipient}
                      label="recipient*"
                      margin="normal"
                      name="recipient"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.recipient}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={3}>
                    <TextField
                      error={Boolean(touched.phone && errors.phone)}
                      fullWidth
                      helperText={touched.phone && errors.phone}
                      label="phone"
                      margin="normal"
                      name="phone"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.phone}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={3}>
                    <TextField
                      error={Boolean(touched.email && errors.email)}
                      fullWidth
                      helperText={touched.email && errors.email}
                      label="email"
                      margin="normal"
                      name="email"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.email}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={4}>
                    <TextField
                      error={Boolean(touched.address1 && errors.address1)}
                      fullWidth
                      helperText={touched.address1 && errors.address1}
                      label="address 1*"
                      margin="normal"
                      name="address1"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.address1}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={4}>
                    <TextField
                      error={Boolean(touched.address2 && errors.address2)}
                      fullWidth
                      helperText={touched.address2 && errors.address2}
                      label="address 2"
                      margin="normal"
                      name="address2"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.address2}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.suburb && errors.suburb)}
                      fullWidth
                      helperText={touched.suburb && errors.suburb}
                      label="suburb *"
                      margin="normal"
                      name="suburb"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.suburb}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.postcode && errors.postcode)}
                      fullWidth
                      helperText={touched.postcode && errors.postcode}
                      label="postcode*"
                      margin="normal"
                      name="postcode"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.postcode}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.state && errors.state)}
                      fullWidth
                      helperText={touched.state && errors.state}
                      label="state*"
                      margin="normal"
                      name="state"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.state}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                </Grid>

                <Box my={2} mx={'auto'} className={classes.buttons}>
                  <Button
                    color="primary"
                    disabled={isSubmitting || loading}
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    {consignmentId ? 'Save' : 'Create'}
                  </Button>
                  <Button
                    disabled={isSubmitting || loading}
                    size="large"
                    variant="contained"
                    type="reset"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        )}
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton
          title={consignmentId ? 'Edit Consignment' : 'Create Consignment'}
        />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateDelivery;
