import { authGet, authPost, authUpdate, noneAuthPost } from './tools';

export class SettingService {
  /**
   * Create a new admin. Before using this api, a message should have been sent to the new admin phone number.
   * @param {String} role superAdminadmin
   */
  static createAdmin = async (payload) => {
    const res = await authPost('platform/accounts/createAdmin', payload);

    return res.data;
  };

  static updateAdmin = async (adminId, payload) => {
    const res = await authUpdate(`platform/accounts/editAdmin/${adminId}`, payload);

    return res.data;
  };

  static fetchAdmins = async ({ currentPage, numberPerPage }) => {
    const res = await authGet('platform/accounts/admin', {
      currentPage,
      numberPerPage,
    });

    return res.data;
  };

  static findDepotlist = async () => {
    const res = await authGet('platform/accounts/findDepotlist');

    return res.data;
  };

  static findAdminById = async (adminId) => {
    const res = await authPost('platform/accounts/findAdminById', { adminId });

    return res.data;
  };
}
