import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import ConsignmentService from 'src/services/consignment';
import GeneralTable from 'src/views/utils/GeneralTable';

// const orderList = [
// 	{ label: 'AZ', value: 'nameAZ' },
// 	{ label: 'ZA', value: 'nameZA' },
// 	{ label: '', value: 'TimeFarest' },
// 	{ label: '', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [consignmentList, setConsignmentList] = useState(null);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setConsignmentList(null);
      setLoading(true);
      try {
        const result = await ConsignmentService.getAllConsignment({
          currentPage: currentPage,
          numberPerPage: limit,
        });
        setCount(result.content.numberOfResults);
        setConsignmentList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/consignment/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/consignment/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await ConsignmentService.deleteConsignment(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Consignment List" onRefresh={handleRefresh}>
              <MainOutLinedButton
                title="Create Consignment"
                onClick={() => {
                  navigate(`/app/consignment/newConsignment`);
                }}
              />
            </BaseTableHeader>
          }
          headers={['DATE', 'TRACKING NUMBER', 'SUPPLIER', 'REFERENCE', 'WEIGHT', 'ACTION']}
          data={consignmentList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {consignmentList?.map((consignment) => (
            <TableRow hover key={consignment._id}>
              <TableCell>
                <Box alignItems="center" display="flex">
                  <Typography color="textPrimary" variant="body1">
                    {consignment.consignmentCompanyId}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>{consignment.trackingNumber}</TableCell>
              <TableCell>{consignment.customerId}</TableCell>
              <TableCell>{consignment.referenceNumber}</TableCell>
              <TableCell>{consignment.weight}</TableCell>
              <TableCell>
                <NoBorderButton
                  title={'EDIT'}
                  type={'primary'}
                  onClick={() => handleEditClick(consignment._id)}
                />
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
