import React from 'react';
import Page from 'src/components/Page';
import GeneralTab from '../utils/GeneralTab';
import { generalStyles } from '../utils/GeneralStyle';
import SmsPushView from './smspush';

const PushView = () => {
  const classes = generalStyles();

  return (
    <Page className={classes.noPaddingRoot} title=''>
      <GeneralTab
        tabTitles={['', 'APP']}
        tabLinks={['/app/pushes/smspush', '/app/pushes/apppush']}
      >
        <SmsPushView />
        <div>app pushj</div>
      </GeneralTab>
    </Page>
  );
};

export default PushView;
