import React, { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate, useParams } from 'react-router';
import {
  Box,
  Button,
  Card,
  CardContent,
  Divider,
  Grid,
  makeStyles,
  Typography,
} from '@material-ui/core';
import FreightstersService from 'src/services/account';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import BaseRouter from 'src/routes/BaseRouter';
import { MainBlueButton } from 'src/views/utils/Buttons';
import BaseLoadingView from 'src/views/utils/BaseLoadingView';
import moment from 'moment';
import DateAndTimePickers from 'src/views/utils/DateAndTimePicker';

const useStyles = makeStyles((theme) => ({
  logoImage: {
    width: '100%',
    paddingRight: 20,
  },
  forbidButton: {
    color: theme.palette.tertiary.main,
  },
  topText: {
    color: theme.palette.text.secondary,
  },
}));

const View = () => {
  const { enqueueSnackbar } = useSnackbar();
  const classes = useStyles();
  const navigate = useNavigate();

  const [customer, setCustomer] = useState(null);

  const { currentPage } = useParams();
  const id = currentPage;

  const [isForbidding, setIsForbidding] = useState(false);

  const [expireDate, setExpireDate] = useState(null);
  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  const handleForbid = async () => {
    setIsForbidding(true);

    try {
      const result = await FreightstersService.forbidCustomer({ id: customer._id });
      setIsForbidding(false);

      setCustomer({
        ...customer,
        forbidden: result.content.forbidden,
      });
      enqueueSnackbar('', {
        variant: 'success',
      });
    } catch (error) {
      setIsForbidding(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const handleActive = async () => {
    setIsForbidding(true);
    setLoading(true);
    try {
      const result = await FreightstersService.activeCustomer({ id: customer._id });
      setIsForbidding(false);

      setCustomer({
        ...customer,
        forbidden: result.content.forbidden,
      });
      enqueueSnackbar('', {
        variant: 'success',
      });
    } catch (error) {
      setIsForbidding(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const handleEdit = () => {
    BaseRouter.editCustomerAccount({
      navigate: navigate,
      customerId: customer._id,
    });
  };

  useEffect(() => {
    const fetch = async () => {
      try {
        const result = await FreightstersService.fetchCustomer({ id: id });

        setCustomer(result.content);
        setExpireDate(moment(result.content?.vipExpireDate).format('YYYY-MM-DD') || null);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      }
    };

    fetch();
  }, [id, refreshFlag, enqueueSnackbar]);

  const handleConfrimVip = async () => {
    setLoading(true);
    setIsForbidding(true);
    try {
      await FreightstersService.editVipStatus({
        id: customer._id,
        vipExpireDate: expireDate,
        vipStatus: true,
        isVip: true,
      });
      setRefreshFlag(!refreshFlag);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      setIsForbidding(false);
    }
  };

  const renderView = () => {
    return (
      <Box>
        <Box mb={3}>
          <GeneralPageTitleWithButton title="">
            <Box>
              {customer && <MainBlueButton title="" onClick={handleEdit} />}
              {customer &&
                (customer.forbidden === true ? (
                  <Button
                    style={{
                      marginRight: 16,
                    }}
                    color="primary"
                    disabled={isForbidding}
                    onClick={handleActive}
                  >
                    
                  </Button>
                ) : (
                  <Button
                    className={classes.forbidButton}
                    disabled={isForbidding}
                    onClick={handleForbid}
                  >
                    
                  </Button>
                ))}
            </Box>
          </GeneralPageTitleWithButton>
        </Box>
        <Card>
          <CardContent>
            <Grid container>
              {customer.iconUrl && (
                <Grid item xs={2}>
                  <img className={classes.logoImage} src={customer.iconUrl} alt="Logo" />
                </Grid>
              )}
              <Grid item xs={customer.iconUrl ? 10 : 12}>
                <Grid container style={{ justifyContent: 'space-between' }}>
                  <Grid item style={{ display: 'flex' }}>
                    <Box mr={3}>
                      <Typography className={classes.topText} variant="h5">
                         {customer.orderCount}
                      </Typography>
                    </Box>
                    <Typography className={classes.topText} variant="h5">
                       {customer.totalAmount}
                    </Typography>
                  </Grid>
                  {/* <Grid item>
                    <MainBlueButton title="" />
                  </Grid> */}
                </Grid>
                <Box mt={2}>
                  <Divider />
                </Box>
                <Box mt={3} mb={3}>
                  <Typography variant="h4">{customer.name}</Typography>
                </Box>

                {[
                  {
                    label: 'ID',
                    value: customer._id,
                  },
                  {
                    label: '',
                    value: customer.isVip ? 'VIP' : '',
                  },
                  {
                    label: '',
                    value: moment(customer.birthday).format('YYYY/MM/DD'),
                  },
                  {
                    label: '',
                    value: moment(customer.registerTime).format('YYYY/MM/DD hh:mm:ss'),
                  },
                ].map((i) => (
                  <Box key={i.label} mt={1}>
                    <Typography color="textPrimary" variant="h5">
                      {i.label}: {i.value}
                    </Typography>
                  </Box>
                ))}

                <Box mt={1}>
                  <Typography color="textPrimary" variant="h5">
                    {''}: {moment(customer.vipExpireDate).format('YYYY-MM-DD')}
                  </Typography>
                </Box>

                {customer._id && (
                  <>
                    <DateAndTimePickers
                      label="VIP"
                      defaultValue={expireDate}
                      onChange={setExpireDate}
                    />
                    <MainBlueButton
                      title="VIP"
                      disabled={loading}
                      onClick={handleConfrimVip}
                    />
                  </>
                )}
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Box>
    );
  };

  return <Box>{customer ? renderView() : <BaseLoadingView />}</Box>;
};

export default View;
