import React from 'react';
import Page from 'src/components/Page';
import { generalStyles } from 'src/views/utils/GeneralStyle';
import GeneralTab from 'src/views/utils/GeneralTab';
import AccountManageView from './AccountManageView';

const SettingsView = () => {
  const classes = generalStyles();

  return (
    <Page className={classes.noPaddingRoot} title="Settings">
      <GeneralTab tabTitles={['Accounts Management']} tabLinks={['/app/settings/accounts']}>
        <AccountManageView />
        <div />
      </GeneralTab>
    </Page>
  );
};

export default SettingsView;
