import React from 'react';
import { Box, CardContent, Grid, Card } from '@material-ui/core';
import { MainBlueButton } from './Buttons';

const GeneralSearchView = ({ children, onSearch, disabled }) => {
  return (
    <Box mt={2} mb={2}>
      <Card>
        <CardContent>
          <Grid container direction="row" justify="space-between" alignItems="center">
            {children}
            <MainBlueButton title="Serach" onClick={onSearch} disabled={disabled} />
          </Grid>
        </CardContent>
      </Card>
    </Box>
  );
};

export default GeneralSearchView;
