import React from 'react';
import Page from 'src/components/Page';
import PickUpView from './pickup';
import PostcodeView from './postcode';
import GeneralTab from 'src/views/utils/GeneralTab';
import { generalStyles } from 'src/views/utils/GeneralStyle';

const PickUpManagementView = () => {
  const classes = generalStyles();

  return (
    <Page className={classes.noPaddingRoot} title="Freightster">
      <GeneralTab
        tabTitles={['Pickup List', 'Postcode List']}
        tabLinks={['/app/pkmanagement/pickup', '/app/pkmanagement/postcode']}
      >
        <PickUpView />
        <PostcodeView />
      </GeneralTab>
    </Page>
  );
};

export default PickUpManagementView;
