class BaseRouter {
  static createShop = ({ navigate }) => navigate(`/app/freightster/corssborder/create`);

  static editShop = ({ navigate, shopId }) => {
    return navigate(`/app/freightster/corssborder/edit/${shopId}`);
  };

  static viewShop = ({ navigate, shopId }) => {
    return navigate(`/app/freightster/corssborder/view/${shopId}`);
  };

  static viewCustomerAccount = ({ navigate, customerId }) => {
    return navigate(`/app/freightster/customer/view/${customerId}`);
  };

  static viewCustomerInvitation = ({ navigate, customerId }) => {
    return navigate(`/app/freightster/customer/invitation/${customerId}`);
  };

  static editCustomerAccount = ({ navigate, customerId }) => {
    return navigate(`/app/freightster/customer/edit/${customerId}`);
  };

  static createCustomerAccount = ({ navigate }) => {
    return navigate(`/app/freightster/customer/create`);
  };
}

export default BaseRouter;
