import React from 'react';
import { Box, CircularProgress } from '@material-ui/core';

const BaseLoader = () => {
  return (
    <Box minWidth={850} mt={3} mb={3} justifyContent="center" display="flex">
      <CircularProgress />
    </Box>
  );
};

const BaseLoaderText = () => {
  return (
    <Box minWidth={850} mt={3} mb={3} justifyContent="center" display="flex">
      Loading...
    </Box>
  );
};

const BaseEmptyText = () => {
  return (
    <Box minWidth={850} mt={3} mb={3} justifyContent="center" display="flex">
      Empty List
    </Box>
  );
};

export { BaseLoader, BaseLoaderText, BaseEmptyText };
