import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import PushService from 'src/services/pushes';
import GeneralTable from 'src/views/utils/GeneralTable';

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [smsList, setSmsList] = useState([]);
  const [count, setCount] = useState(0);
  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setSmsList([]);
      setLoading(true);
      try {
        const result = await PushService.getSMSPush({
          currentPage: currentPage,
          numberPerPage: limit,
        });
        setCount(result.content.numberOfResults);
        setSmsList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/tags/producttags/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/tags/producttags/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      //await PushService.deleteProductTag(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="" onRefresh={handleRefresh}>
              <MainOutLinedButton
                title="Create"
                onClick={() => {
                  navigate(`/app/tags/newproducttag`);
                }}
              />
            </BaseTableHeader>
          }
          headers={['code', '', '', '', '', 'Create', '']}
          data={!loading}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
        >
          {smsList.length > 0 &&
            smsList.map((tag, index) => (
              <TableRow hover key={tag._id}>
                <TableCell>
                  {tag.logoUrl && (
                    <img alt="LOGO" src={tag.logoUrl} style={{ height: 60, width: 60 }} />
                  )}
                </TableCell>
                <TableCell>{tag.tagName}</TableCell>
                <TableCell>{''}</TableCell>
                <TableCell>{tag.weight}</TableCell>
                <TableCell>{tag.subTags.length}</TableCell>
                <TableCell>
                  <Box alignItems="center" display="flex">
                    <Typography color="textPrimary" variant="body1">
                      <NoBorderButton
                        title={''}
                        type={'primary'}
                        disabled={loading}
                        onClick={() => {}}
                      />
                      <NoBorderButton
                        title={''}
                        disabled={loading}
                        onClick={() => {}}
                      />
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell>
                  <NoBorderButton
                    title={''}
                    type={'primary'}
                    disabled={loading}
                    onClick={() => handleEditClick(tag._id)}
                  />
                  <NoBorderButton
                    title={''}
                    type={'secondary'}
                    disabled={loading}
                    onClick={() => handleDeleteClick(tag._id)}
                  />
                </TableCell>
              </TableRow>
            ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  smsList: PropTypes.array,
};

export default Results;
