import { authGet, authPost, authDelete, authUpdate } from './tools';

class PickupService {
  static getAllPickup = async ({ currentPage, numberPerPage }) => {
    const params = {
      currentPage,
      numberPerPage,
    };

    const res = await authGet('platform/pickup/findPickUps', params);

    return res.data;
  };

  static importExcel = async (file) => {
    const res = await authPost('platform/pickup/importPickUpConsignmentExcel', file);
  };

  //Get a specific pickup.
  static findPickupById = async (id) => {
    const res = await authGet(`platform/pickup/findPickupById/${id}`);
    return res.data;
  };

  //Create a pickup.
  static createPickup = async (params) => {
    const res = await authPost('platform/pickup/createPickup', params);
    return res.data;
  };

  //Update a pickup.
  static editPickup = async (pickupId, payload) => {
    const res = await authUpdate(`platform/pickup/editPickup/${pickupId}`, payload);
    return res.data;
  };

  //Delete a named pickup.
  static deletePickup = async (id) => {
    const res = await authDelete(`platform/pickup/${id}`);

    return res.data;
  };
}

export default PickupService;
