import { authGet, authPost, authUpdate } from './tools';

class FreightstersService {
  static findFreightsters = async ({ nameOrEmailOrPhoneNumber, numberPerPage, currentPage }) => {
    const params = {
      nameOrEmailOrPhoneNumber,
      numberPerPage,
      currentPage,
    };
    const res = await authGet('platform/freightster/findFreightsters', params);

    return res.data;
  };

  static importExcel = async (file) => {
    const res = await authPost('platform/freightster/importExcel', file);
  };
}

export default FreightstersService;
