import React from 'react';
import { connect } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import { Box, Button, Container, TextField, Typography, Link, makeStyles } from '@material-ui/core';
import Page from 'src/components/Page';
import AuthService from 'src/services/auth';
import { setCurrentUser } from 'src/redux/authAction';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3),
  },
}));

const LoginView = ({ setCurrentUser }) => {
  const { enqueueSnackbar } = useSnackbar();
  const classes = useStyles();
  const navigate = useNavigate();

  const login = async (value) => {
    const phoneNumber = value.account;
    const password = value.password;

    try {
      const result = await AuthService.loginWith({
        phone: phoneNumber,
        password: password,
      });

      const userInfo = result.content.user;
      if (['SuperAdmin', 'Admin'].indexOf(userInfo.role) < 0) {
        throw new Error('');
      }

      const token = result.content.token;

      const authInfo = {
        token: token,
        _id: userInfo._id,
        phoneNumber: userInfo.phoneNumber,
        name: userInfo.name,
        role: userInfo.role,
      };

      setCurrentUser(authInfo);
      enqueueSnackbar(result.message, { variant: 'success' });
      navigate('/app/dashboard', { replace: true });
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  return (
    <Page className={classes.root} title="Login">
      <Box display="flex" flexDirection="column" height="100%" justifyContent="center">
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              account: '',
              password: '',
            }}
            validationSchema={Yup.object().shape({
              account: Yup.string().max(255).required(''),
              password: Yup.string().max(255).required(''),
            })}
            onSubmit={login}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mb={3}>
                  <Typography color="textPrimary" variant="h2">
                    
                  </Typography>
                  <Typography color="textSecondary" gutterBottom variant="body2">
                    
                  </Typography>
                </Box>
                <TextField
                  error={Boolean(touched.account && errors.account)}
                  fullWidth
                  helperText={touched.account && errors.account}
                  label="/"
                  margin="normal"
                  name="account"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.account}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label=""
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="password"
                  value={values.password}
                  variant="outlined"
                />
                <Box my={2}>
                  <Button
                    color="primary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    
                  </Button>
                </Box>
                {/* <Typography color='textSecondary' variant='body1'>
									Don&apos;t have an account?{' '}
									<Link component={RouterLink} to='/register' variant='h6'>
										Sign up
									</Link>
								</Typography> */}
              </form>
            )}
          </Formik>
          <Link component="button" onClick={() => navigate('/password')}>
            
          </Link>
        </Container>
      </Box>
    </Page>
  );
};

const mapStateToProps = ({ auth }) => {
  return { auth };
};

export default connect(mapStateToProps, { setCurrentUser })(LoginView);
