import React, { useEffect, useState } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
  TableCell,
  TableRow,
  TextField,
  Typography,
} from '@material-ui/core';
import { useSnackbar } from 'notistack';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import { useNavigate, useParams } from 'react-router';
import TicketService from 'src/services/ticket';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import GeneralTable from 'src/views/utils/GeneralTable';
import moment from 'moment';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
  editor: {
    border: '1px solid blue',
    width: '50%',
  },
}));

const CreateTicket = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);
  const [detailList, setDetailList] = useState([]);
  const [openEditor, setEditorOpen] = useState(false);
  const [reply, setReply] = useState('');

  const { ticketId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await TicketService.findTicketById(id);
        setDetailList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    ticketId ? fetchResults(ticketId) : handleOpenEditor();
  }, [ticketId, refreshFlag, enqueueSnackbar]);

  const navigate = useNavigate();

  const handleOpenEditor = () => {
    setEditorOpen(true);
  };

  const handleCloseTicket = async () => {
    try {
      setIsSubmitting(true);
      await TicketService.editTicket(ticketId);
      enqueueSnackbar('Ticket closed', { variant: 'success' });
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    navigate(`/app/ticket`);
  };

  const submitReply = async () => {
    try {
      setIsSubmitting(true);
      let payload = {
        ticketId,
        description: reply,
      };
      if (ticketId) {
        await TicketService.createTicketDetail(payload);
      } else {
        await TicketService.createTicket(payload);
        navigate(`/app/ticket`);
      }
      enqueueSnackbar('Ticket closed', { variant: 'success' });
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setRefreshFlag(!refreshFlag);
      ticketId && setEditorOpen(false);
      setIsSubmitting(false);
    }
  };

  const handleCloseEdit = () => {
    if (ticketId) {
      setEditorOpen(false);
      setReply('');
    } else {
      handleCancel();
    }
  };

  const renderContent = () => (
    <Grid container>
      <GeneralTable
        baseTableHeader={<BaseTableHeader title="Ticket Detail"></BaseTableHeader>}
        headers={['DESCRIPTION', 'ADMIN', 'TIME']}
        data={detailList}
        loading={loading}
      >
        {detailList?.map((ticket) => (
          <TableRow hover key={ticket._id}>
            <TableCell>{ticket.description}</TableCell>
            <TableCell>{ticket.admin}</TableCell>
            <TableCell>{moment(ticket.createTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
          </TableRow>
        ))}
      </GeneralTable>
    </Grid>
  );

  const renderOptions = () => {
    return (
      <Box my={2} mx={'auto'} className={classes.buttons}>
        <Button
          color="primary"
          disabled={isSubmitting || loading}
          size="large"
          type="submit"
          variant="contained"
          onClick={handleOpenEditor}
        >
          New Reply
        </Button>
        <Button
          color="primary"
          disabled={isSubmitting || loading}
          size="large"
          type="submit"
          variant="contained"
          onClick={handleCloseTicket}
        >
          Close Ticket
        </Button>
        <Button
          disabled={isSubmitting || loading}
          size="large"
          variant="contained"
          type="reset"
          onClick={handleCancel}
        >
          Cancel
        </Button>
      </Box>
    );
  };

  const renderEditor = () => {
    return (
      <Grid container spacing={1}>
        <Grid item xs={10}>
          <TextField
            className={classes.editor}
            label="Description"
            multiline
            rows={4}
            value={reply}
            onChange={(e) => {
              setReply(e.target.value);
            }}
          />
        </Grid>
        <Grid item container xs={10} spacing={2}>
          <Grid item>
            <Button
              color="primary"
              disabled={isSubmitting || loading}
              size="large"
              type="submit"
              variant="contained"
              onClick={submitReply}
            >
              Submit
            </Button>
          </Grid>
          <Grid item>
            <Button
              disabled={isSubmitting || loading}
              size="large"
              variant="contained"
              type="reset"
              onClick={handleCloseEdit}
            >
              Cancel
            </Button>
          </Grid>
        </Grid>
      </Grid>
    );
  };

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={ticketId ? 'Edit Ticket' : 'Create Ticket'} />
      </Box>
      <Card>
        <CardContent>{detailList?.length > 0 && renderContent()}</CardContent>
        <CardContent>{ticketId && !openEditor && renderOptions()}</CardContent>
        <CardContent>{openEditor && renderEditor()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateTicket;
