import React, { useState, useEffect, useRef } from 'react';
import { useSnackbar } from 'notistack';
import { Box, Button, Card, CardContent, Grid, makeStyles, TextField } from '@material-ui/core';
import InboundService from 'src/services/inbound';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';

const useStyles = makeStyles((theme) => ({
  logoImage: {
    width: '100%',
    paddingRight: 20,
  },
  forbidButton: {
    color: theme.palette.tertiary.main,
  },
  topText: {
    color: theme.palette.text.secondary,
  },
  form: {
    width: '60%',
  },
  fullWidth: {
    width: '100%',
  },
}));

const View = () => {
  const ref = useRef(null);
  const { enqueueSnackbar } = useSnackbar();
  const classes = useStyles();

  const [inboundDetail, setInboundDetail] = useState({});
  const [trackingNumber, setTrackingNumber] = useState('');

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(true);

  useEffect(() => {}, [refreshFlag, enqueueSnackbar]);

  const handleTrackingNumberChange = (e) => {
    setTrackingNumber(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!trackingNumber?.length > 0) {
      return;
    }
    ref.current.focus();
    try {
      setLoading(true);
      const inboundRes = await InboundService.inboundcheck(trackingNumber);
      setInboundDetail(inboundRes.content);
      await InboundService.inboundConfirm(trackingNumber);
      setRefreshFlag(false);
      setTrackingNumber('');
      enqueueSnackbar('Success to inbound', { variant: 'success' });
    } catch (error) {
      enqueueSnackbar('Failed to inbound', { variant: 'error' });
    } finally {
      setRefreshFlag(true);
      setLoading(false);
    }
  };

  const renderView = () => {
    return (
      <Box>
        <Box mb={3}>
          <GeneralPageTitleWithButton title=""></GeneralPageTitleWithButton>
        </Box>
        {refreshFlag && (
          <Card>
            <CardContent>
              <Grid container className={classes.form}>
                <form className={classes.form} onSubmit={handleSubmit}>
                  <Grid
                    container
                    item
                    xs={12}
                    spacing={2}
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <Grid item xs={9}>
                      <TextField
                        label="Tracking Number"
                        margin="normal"
                        variant="outlined"
                        disabled={loading}
                        value={trackingNumber}
                        onChange={handleTrackingNumberChange}
                        className={classes.fullWidth}
                        autoFocus
                        ref={ref}
                      />
                    </Grid>
                    <Grid item xs={3}>
                      <Button
                        color="primary"
                        disabled={loading}
                        size="large"
                        type="submit"
                        variant="contained"
                      >
                        CHECK
                      </Button>
                    </Grid>
                  </Grid>
                </form>
                <Grid className={classes.form} container item xs={12} spacing={3}>
                  <Grid item xs={5}>
                    ReferenceNumber: {inboundDetail?.referenceNumber}
                  </Grid>
                  <Grid item xs={5}>
                    Postcode: {inboundDetail?.postcode}
                  </Grid>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        )}
      </Box>
    );
  };

  return <Box>{renderView()}</Box>;
};

export default View;
