import React from 'react';
import { Container } from '@material-ui/core';
import Page from 'src/components/Page';
import CreatePostcode from './CreatePostcode';
import { generalStyles } from 'src/views/utils/GeneralStyle';
import { useParams } from 'react-router';

const PostcodeForm = () => {
  const classes = generalStyles();
  const id = useParams();

  return (
    <Page className={classes.root} title={id ? 'Create Postcode' : 'Eidt Postcode'}>
      <Container maxWidth={false}>
        <Container>
          <CreatePostcode />
        </Container>
      </Container>
    </Page>
  );
};

export default PostcodeForm;
