import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, TextField, Button } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import Toolbar from '../../Toolbar';
import GeneralTable from 'src/views/utils/GeneralTable';
import FreightstersService from 'src/services/account';
// import MainOutLinedButton from 'src/views/utils/Buttons';
import Cell from './Cell';
// import BaseRouter from 'src/routes/BaseRouter';

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [freightsters, setFreightsters] = useState(null);
  const [count, setCount] = useState(0);
  const initSearchField = {
    searchText: '',
    startDate: null,
    endDate: null,
  };
  const [refreshFlag, setRefreshFlag] = useState(false);
  const [searchFields, setSearchFields] = useState(initSearchField);
  const [selectedFile, setSelectedFile] = useState();
  const [loading, setLoading] = useState(false);

  const fetchResults = async () => {
    setFreightsters(null);
    try {
      const result = await FreightstersService.findFreightsters({
        currentPage: currentPage,
        numberPerPage: limit,
        nameOrEmailOrPhoneNumber: searchFields.searchText,
        createTimeStart: searchFields.startDate,
        createTimeEnd: searchFields.endDate,
      });
      setCount(result.content.numberOfResults);
      setFreightsters(result.content.results);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  useEffect(() => {
    fetchResults();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentPage, limit, searchFields, refreshFlag, enqueueSnackbar]);

  const changeHandler = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const handleSubmission = async () => {
    setLoading(true);
    const formData = new FormData();
    formData.append('file', selectedFile);

    try {
      await FreightstersService.importExcel(formData);
      enqueueSnackbar('File has been uploaded successfully', { variant: 'success' });
      setSelectedFile(null);
      setRefreshFlag(!refreshFlag);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/freightster/corssborder/list/${newPage + 1}`);
  };

  return (
    <Box>
      <Toolbar
        placeholder="Numbers"
        onSearch={(text, startDate, endDate) => {
          setSearchFields({
            searchText: text,
            startDate: startDate,
            endDate: endDate,
          });
        }}
      />
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader
              title="Result List"
              onRefresh={() => {
                setRefreshFlag(!refreshFlag);
              }}
            >
              <a href="/file/freightster.xls" download>
                Donload Sample file
              </a>
              <TextField
                type="file"
                name="file"
                variant="outlined"
                onChange={changeHandler}
                disabled={loading}
              />
              <Button
                color="primary"
                style={{ marginLeft: '30px' }}
                disabled={!selectedFile || loading}
                size="large"
                variant="contained"
                onClick={handleSubmission}
              >
                Upload
              </Button>
            </BaseTableHeader>
          }
          headers={[
            'Freightster Id',
            'Loading Port',
            'Weight Kg',
            'Flight Number',
            'Departure Time',
            'Total',
            'A-Scan',
            'Delivered Rate',
          ]}
          data={freightsters}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
        >
          {freightsters?.map((freightster, index) => (
            <Cell initFreightster={freightster} key={index} />
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

export default Results;
