import axios from 'axios';
import { getAuthToken } from 'src/redux/storeAccess';
import { BASEURL } from './constants';
import GlobalSerivce from './global';

class NetworkError extends Error
{
  constructor(code, message)
  {
    const fullMessage = message ? `${ code }: ${ message }` : code;
    super(fullMessage);
    this.name = code;
    this.code = code;
    this.message = fullMessage;
  }

  toString()
  {
    return this.message;
  }
}

export const noneAuthInstance = axios.create({
  // Bypass CORS
  baseURL: BASEURL,
  timeout: 30000,
});

export const authInstance = axios.create({
  // Bypass CORS
  baseURL: BASEURL,
  timeout: 30000,
});

export const noneAuthPost = async (url, params) =>
{
  try
  {
    return await noneAuthInstance.post(url, params);
  } catch (error)
  {
    throw new NetworkError(error.response?.status, error.response?.data?.message ?? error.message);
  }
};

export const authPost = async (url, params) =>
{
  authInstance.defaults.headers.common = {
    Authorization: `Bearer ${ getAuthToken() }`,
  };

  try
  {
    const response = await authInstance.post(url, params);
    return response;
  } catch (error)
  {
    if (error.response?.data?.code === 401)
    {
      GlobalSerivce.logout();
    }

    throw new NetworkError(error.response?.status, error.response?.data?.message ?? error.message);
  }
};

export const authGet = async (url, params) =>
{
  authInstance.defaults.headers.common = {
    Authorization: `Bearer ${ getAuthToken() }`,
  };

  try
  {
    const response = await authInstance.get(url, { params: params });

    return response;
  } catch (error)
  {
    if (error.response?.data?.code === 401)
    {
      GlobalSerivce.logout();
    }
    throw new NetworkError(error.response?.status, error.response?.data?.message ?? error.message);
  }
};

