import React, { useEffect, useState } from 'react';
import { Box, Button, Card, CardContent, Grid, makeStyles, TextField } from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import SupplierService from 'src/services/supplier';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
}));

const CreateDelivery = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [initialValues, setInitialValues] = useState({
    name: '',
    password: '',
    email: '',
    supplierId: '',
    depot: '',
    Requestor: '',
    commenceDate: '',
    workTime: '',
    team: '',
    vehicle: '',
    phone: '',
    receivingInvoiceEmail: '',
    supplierLicence: '',
    address: '',
    suburb: '',
    postcode: '',
    company: '',
    ABN: '',
    bankDetail: '',
    BSB: '',
    accountNumber: '',
    emergencyContactPerson: '',
    emergencyContactNumber: '',
    isPickUp: '',
    isinboundScan: '',
    isRelatedtoFreightster: '',
    enableInvoice: '',
    supplierLicenceimage: '',
    policeCheckimage: '',
  });

  const { supplierId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await SupplierService.findSupplierById(id);
        let newValues = initialValues;
        Object.keys(initialValues).forEach((key) => {
          newValues[key] = result.content[key];
        });
        setInitialValues(newValues);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    supplierId && fetchResults(supplierId);
  }, [supplierId, enqueueSnackbar]);

  const navigate = useNavigate();

  const requestWith = async (values, actions) => {
    try {
      await SupplierService.createSupplier({
        customerId: values.customerId,
        trackingNumber: values.trackingNumber,
        recipient: values.recipient,
        phone: values.phone,
        email: values.email,
        address1: values.address1,
        address2: values.address2,
        suburb: values.suburb,
        state: values.state,
        postcode: values.state,
      });

      enqueueSnackbar('Create', { variant: 'success' });

      navigate('/app/supplier');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const updateDelivery = async (values, actions, id) => {
    try {
      await SupplierService.editSupplier(supplierId, values);

      enqueueSnackbar('', { variant: 'success' });

      navigate('/app/supplier');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const handleFormSubmit = (values, actions) => {
    !supplierId ? requestWith(values, actions) : updateDelivery(values, actions, supplierId);
  };

  const handleCancel = () => {
    navigate(`/app/supplier`);
  };

  const renderContent = () => (
    <Grid container>
      <Grid item container xs={12}>
        {(!supplierId || (supplierId && initialValues.name)) && (
          <Formik
            initialValues={initialValues}
            validationSchema={Yup.object().shape({
              name: Yup.string().max(255).required('*required'),
              password: Yup.string().max(255).required('*required'),
              email: Yup.string().email().max(255).required('*required'),
              supplierId: Yup.string().max(255).required('*required'),
              depot: Yup.string().max(255).required('*required'),
            })}
            onSubmit={(values, actions) => {
              handleFormSubmit(values, actions);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit} className={classes.form}>
                <Grid item container xs={12} spacing={2}>
                  {Object.keys(initialValues)
                    .slice(0, 21)
                    .map((key) => (
                      <Grid item xs={2} key={key}>
                        <TextField
                          error={Boolean(touched[key] && errors[key])}
                          fullWidth
                          helperText={touched[key] && errors[key]}
                          label={key}
                          margin="normal"
                          name={key}
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values[key]}
                          variant="outlined"
                          disabled={isSubmitting || loading}
                        />
                      </Grid>
                    ))}
                  <Grid item xs={6} />
                  <Grid item xs={3}>
                    <TextField
                      error={Boolean(
                        touched.emergencyContactPerson && errors.emergencyContactPerson,
                      )}
                      fullWidth
                      helperText={touched.emergencyContactPerson && errors.emergencyContactPerson}
                      label="emergencyContactPerson"
                      margin="normal"
                      name="emergencyContactPerson"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.emergencyContactPerson}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                    />
                  </Grid>
                  <Grid item xs={4}>
                    <TextField
                      error={Boolean(
                        touched.emergencyContactNumber && errors.emergencyContactNumber,
                      )}
                      fullWidth
                      helperText={touched.emergencyContactNumber && errors.emergencyContactNumber}
                      label="emergencyContactNumber"
                      margin="normal"
                      name="emergencyContactNumber"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.emergencyContactNumber}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                    />
                  </Grid>
                </Grid>

                <Box my={2} mx={'auto'} className={classes.buttons}>
                  <Button
                    color="primary"
                    disabled={isSubmitting || loading}
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    {supplierId ? 'Save' : 'Create'}
                  </Button>
                  <Button
                    disabled={isSubmitting || loading}
                    size="large"
                    variant="contained"
                    type="reset"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        )}
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={supplierId ? 'Edit Supplier' : 'Create Supplier'} />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateDelivery;
