import React, { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
import { useParams } from 'react-router';
import * as Yup from 'yup';
import {
  Box,
  Button,
  Card,
  CardContent,
  Divider,
  Grid,
  makeStyles,
  TableRow,
  TableCell,
  Typography,
  TextField,
} from '@material-ui/core';
import { Formik } from 'formik';
import FreightstersService from 'src/services/account';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
// import BaseRouter from 'src/routes/BaseRouter';
import { MainBlueButton } from 'src/views/utils/Buttons';
import ToggleButtons from 'src/views/utils/ToggleButtons';
import BaseLoadingView from 'src/views/utils/BaseLoadingView';
import GeneralTable from 'src/views/utils/GeneralTable';
import moment from 'moment';

const useStyles = makeStyles((theme) => ({
  logoImage: {
    width: '100%',
    paddingRight: 20,
  },
  forbidButton: {
    color: theme.palette.tertiary.main,
  },
  topText: {
    color: theme.palette.text.secondary,
  },
}));

const View = () => {
  const { enqueueSnackbar } = useSnackbar();
  const classes = useStyles();
  // const navigate = useNavigate();

  const [customer, setCustomer] = useState(null);

  const { currentPage } = useParams();
  const id = currentPage;

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);
  const [edit, setEdit] = useState(false);
  const [initialValue, setInitialValue] = useState({
    isProxy: false,
    invitecode: '',
    discount: '',
  });
  const [inviteList, setInviteList] = useState([]);

  useEffect(() => {
    const fetch = async () => {
      try {
        const [result, listResult] = await Promise.all([
          FreightstersService.fetchCustomer({ id: id }),
          FreightstersService.getInviteCustomerinfo({ customerid: id, numberPerPage: 100 }),
        ]);
        const { isProxy, invitecode, discount } = result.content;
        setCustomer(result.content);
        setInviteList(listResult.content.results);
        setInitialValue({
          isProxy: isProxy || false,
          invitecode: invitecode || '',
          discount: discount || '',
        });
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      }
    };

    fetch();
  }, [id, refreshFlag, enqueueSnackbar]);

  const handleFormSubmit = async (values, actions) => {
    setLoading(true);
    actions.setSubmitting(true);

    try {
      values.isProxy &&
        initialValue.invitecode !== values.invitecode &&
        (await FreightstersService.checkInviteCode({ invitecode: values.invitecode }));
      await FreightstersService.editVipStatus({
        id: customer._id,
        isProxy: values.isProxy,
        invitecode: values.invitecode,
        discount: values.discount,
      });
      setRefreshFlag(!refreshFlag);
      enqueueSnackbar('', { variant: 'success' });
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      actions.setSubmitting(false);
    }
  };

  const handleEnableEdit = () => setEdit(!edit);

  const invitationDetail = ({ isProxy, invitecode, discount }) => (
    <Box mt={10}>
      <Typography>{`${isProxy ? '' : ''}`}</Typography>
      <Typography>{`${isProxy && invitecode ? invitecode : ''}`}</Typography>
      <Typography>{`${isProxy && discount ? discount : ''}`}</Typography>
      <MainBlueButton title="" onClick={handleEnableEdit} />
    </Box>
  );

  const invitationForm = () => (
    <Box mt={2} style={{ width: '50%' }}>
      <Formik
        initialValues={initialValue}
        validationSchema={Yup.object().shape({
          isProxy: Yup.boolean(),
          invitecode: Yup.string()
            .min(4)
            .max(8)
            .when('isProxy', {
              is: true,
              then: Yup.string().required('').typeError('4-8'),
              otherwise: (schema) => schema.notRequired(),
            }),
          discount: Yup.number('')
            .min(0.01)
            .max(68)
            .when('isProxy', {
              is: true,
              then: Yup.number().required('').typeError(''),
              otherwise: (schema) => schema.notRequired(),
            }),
        })}
        onSubmit={(values, actions) => {
          handleFormSubmit(values, actions);
        }}
      >
        {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
          <form onSubmit={handleSubmit}>
            <ToggleButtons
              labels={['', '']}
              value={values.isProxy || false}
              name="isProxy"
              onSelect={handleChange}
              desc="*"
              disabled={isSubmitting || loading}
            />
            {values.isProxy && (
              <>
                <TextField
                  error={Boolean(touched.invitecode && errors.invitecode)}
                  fullWidth
                  helperText={touched.invitecode && errors.invitecode}
                  label=""
                  margin="normal"
                  name="invitecode"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.isProxy ? values.invitecode : ''}
                  disabled={loading || !values.isProxy}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.discount && errors.discount)}
                  fullWidth
                  helperText={touched.discount && errors.discount}
                  label=""
                  margin="normal"
                  name="discount"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.isProxy ? values.discount : ''}
                  disabled={loading || !values.isProxy}
                  variant="outlined"
                />
              </>
            )}
            <Grid container spacing={2} style={{ display: 'inline-flex' }}>
              <Grid item xs={4}>
                <Button
                  color="primary"
                  disabled={isSubmitting}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                >
                  
                </Button>
              </Grid>
              <Grid item xs={4}>
                <Button
                  color="primary"
                  disabled={isSubmitting}
                  fullWidth
                  size="large"
                  onClick={handleEnableEdit}
                  variant="contained"
                >
                  Cancel
                </Button>
              </Grid>
            </Grid>
          </form>
        )}
      </Formik>
    </Box>
  );

  const renderInviteList = (inviteList) => (
    <GeneralTable
      baseTableHeader={<BaseTableHeader title=""></BaseTableHeader>}
      headers={['ID', 'Logo', '', '', '', '']}
      data={inviteList}
      noTablePagination={true}
    >
      {inviteList?.map((item) => (
        <TableRow hover key={item?.customerid?.id}>
          <TableCell>
            <Box alignItems="center" display="flex">
              <Typography color="textPrimary" variant="body1">
                {item?.customerid?._id}
              </Typography>
            </Box>
          </TableCell>

          <TableCell>
            {item.iconUrl && (
              <img alt="LOGO" src={item?.customerid?.iconUrl} style={{ height: 60, width: 60 }} />
            )}
          </TableCell>
          <TableCell>{item?.customerid?.name}</TableCell>
          <TableCell>{item?.customerid?.phoneNumber}</TableCell>
          <TableCell>{item?.customerid?.vipStatus === true ? '' : ''}</TableCell>
          <TableCell>{moment(item?.createTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
        </TableRow>
      ))}
    </GeneralTable>
  );

  const renderView = () => {
    return (
      <Box>
        <Box mb={3}>
          <GeneralPageTitleWithButton title=""></GeneralPageTitleWithButton>
        </Box>
        <Card>
          <CardContent>
            <Grid container>
              {customer.iconUrl && (
                <Grid item xs={2}>
                  <img className={classes.logoImage} src={customer.iconUrl} alt="Logo" />
                </Grid>
              )}
              <Grid item xs={customer.iconUrl ? 10 : 12}>
                <Grid container style={{ justifyContent: 'space-between' }}>
                  <Grid item style={{ display: 'flex' }}>
                    <Box mr={3}>
                      <Typography className={classes.topText} variant="h5">
                         {customer.orderCount}
                      </Typography>
                    </Box>
                    <Typography className={classes.topText} variant="h5">
                       {customer.totalAmount}
                    </Typography>
                  </Grid>
                  {/* <Grid item>
                    <MainBlueButton title="" />
                  </Grid> */}
                </Grid>
                <Box mt={2}>
                  <Divider />
                </Box>
                <Grid container>
                  <Grid item xs={4}>
                    <Box mt={3} mb={3}>
                      <Typography variant="h4">{customer.name}</Typography>
                    </Box>

                    {[
                      {
                        label: 'ID',
                        value: customer._id,
                      },
                      {
                        label: '',
                        value: customer.isVip ? 'VIP' : '',
                      },
                      {
                        label: '',
                        value: moment(customer.birthday).format('YYYY/MM/DD'),
                      },
                      {
                        label: '',
                        value: moment(customer.registerTime).format('YYYY/MM/DD hh:mm:ss'),
                      },
                    ].map((i) => (
                      <Box key={i.label} mt={1}>
                        <Typography color="textPrimary" variant="h5">
                          {i.label}: {i.value}
                        </Typography>
                      </Box>
                    ))}
                  </Grid>
                  <Grid item xs={8}>
                    {edit ? invitationForm() : invitationDetail(customer)}
                  </Grid>
                  <Grid item xs={12}>
                    {customer.isProxy && !inviteList?.length > 0 && <h3>{''}</h3>}
                    {inviteList?.length > 0 && renderInviteList(inviteList)}
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Box>
    );
  };

  return <Box>{customer ? renderView() : <BaseLoadingView />}</Box>;
};

export default View;
