import React from 'react';
import { AppBar, Paper, Tabs, Tab, Box, makeStyles } from '@material-ui/core';
import { useParams, useNavigate } from 'react-router';

const useStyles = makeStyles(() => ({
  root: {},
  bigFont: {
    fontSize: '1rem',
    fontWeight: 400,
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

/**
 *
 * @param {[String]} tabTitles Tab title text in tab bar.
 * @param {[String]} tabLinks Tab nav URL in tab bar.
 * @param {Array} children Views in tab pannel
 */
const GeneralTab = ({ tabTitles, tabLinks, children }) => {
  const classes = useStyles();
  const { type } = useParams();
  const navigate = useNavigate();

  const renderSelectIndex = () => (type ? tabLinks.findIndex((i) => i.includes(type)) : 0);

  return (
    <>
      <AppBar position="static">
        <Paper variant="outlined" square>
          <Tabs value={renderSelectIndex()} indicatorColor="secondary" textColor="inherit">
            {tabTitles.map((i, index) => (
              <Tab
                className={classes.bigFont}
                label={i}
                key={index}
                onClick={() => {
                  navigate(tabLinks[index], { replace: true });
                }}
              />
            ))}
          </Tabs>
        </Paper>
      </AppBar>

      {/*when there is only one child, map wont work, render it directly.*/}
      {children.length > 1
        ? children.map((i, index) => (
            <TabPanel key={index} value={renderSelectIndex()} index={index}>
              {i}
            </TabPanel>
          ))
        : children}
    </>
  );
};

export default GeneralTab;
