import React, { useEffect, useState } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
  TextField,
  TableCell,
  TableRow,
  Typography,
} from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import moment from 'moment';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import GeneralTable from 'src/views/utils/GeneralTable';
import { useNavigate, useParams } from 'react-router';
import JobService from 'src/services/job';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
}));

const CreateDelivery = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [consignmentList, setConsignmentList] = useState([]);

  const { jobId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await JobService.findJobById(id);
        setConsignmentList(result.content);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    jobId && fetchResults(jobId);
  }, [jobId, enqueueSnackbar]);

  const navigate = useNavigate();

  const updateDelivery = async (values, actions, id) => {
    try {
      await JobService.editJob(jobId, values);

      enqueueSnackbar('', { variant: 'success' });

      navigate('/app/consignment');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const renderContent = () => (
    <Grid container>
      <Grid item container xs={12}>
        <GeneralTable
          baseTableHeader={<BaseTableHeader title="Consignment List"></BaseTableHeader>}
          headers={[
            'SEQUENCE',
            'CONSIGNMENT NUMBER',
            'ONBAORD DATE',
            'COMPLETE DATE',
            'RECEIVER',
            'PHONE',
            'ADDRESS',
            'SUBURB',
            'POSTCODE',
            'STATE',
            'DISTANCE',
            'EST.TIME',
            'ACTUAL TIME',
            'STATUS',
            'ACTION',
          ]}
          data={consignmentList}
          loading={loading}
        >
          {consignmentList?.map((job) => (
            <TableRow hover key={job._id}>
              <TableCell>
                <Box alignItems="center" display="flex">
                  <Typography color="textPrimary" variant="body1">
                    {job.sequence}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>{job.consignmentNumber}</TableCell>
              <TableCell>{moment(job.onboardDate).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>{moment(job.completeDate).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>{job.receiver}</TableCell>
              <TableCell>{job.phone}</TableCell>
              <TableCell>{job.address}</TableCell>
              <TableCell>{job.suburb}</TableCell>
              <TableCell>{job.postcode}</TableCell>
              <TableCell>{job.state}</TableCell>
              <TableCell>{job.distance}</TableCell>
              <TableCell>{job.EstTime}</TableCell>
              <TableCell>{}</TableCell>
              <TableCell>{job.status}</TableCell>
              <TableCell>
                {/* <NoBorderButton title={'DELIVER'} type={'primary'} onClick={() => {}} />
                <NoBorderButton title={'RETURN'} type={'primary'} onClick={() => {}} />
                <NoBorderButton title={'CANCEL'} type={'primary'} onClick={() => {}} /> */}
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={jobId ? 'Edit Job' : 'Create Job'} />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateDelivery;
