import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, Button, TableCell, TableRow, TextField, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import PickupService from 'src/services/pickup';
import DriverService from 'src/services/driver';
import GeneralTable from 'src/views/utils/GeneralTable';
import moment from 'moment';

// const orderList = [
// 	{ label: 'AZ', value: 'nameAZ' },
// 	{ label: 'ZA', value: 'nameZA' },
// 	{ label: '', value: 'TimeFarest' },
// 	{ label: '', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [pickupList, setPickupList] = useState(null);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);
  const [selectedFile, setSelectedFile] = useState();
  const [driverList, setDriverList] = useState([]);
  const [selectedDriver, setSelectedDriver] = useState({});

  useEffect(() => {
    const fetchResults = async () => {
      setPickupList(null);
      setLoading(true);
      try {
        const [result, driverRes] = await Promise.all([
          PickupService.getAllPickup({
            currentPage: currentPage,
            numberPerPage: limit,
          }),
          DriverService.getAllDriver(1, 999),
        ]);
        setCount(result.content.numberOfResults);
        setPickupList(result.content.results);
        setDriverList(driverRes.content?.results || []);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/pickup/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/pickup/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await PickupService.deletePickup(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  const changeHandler = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const handleSubmission = async () => {
    setLoading(true);
    const formData = new FormData();
    formData.append('file', selectedFile);

    try {
      await PickupService.importExcel(formData);
      enqueueSnackbar('File has been uploaded successfully', { variant: 'success' });
      setSelectedFile(null);
      setRefreshFlag(!refreshFlag);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Pickup List" onRefresh={handleRefresh}>
              <a href="/file/pickup.xls" download>
                Donload Sample file
              </a>
              <TextField
                type="file"
                name="file"
                variant="outlined"
                onChange={changeHandler}
                disabled={loading}
              />
              <Button
                color="primary"
                style={{ marginLeft: '30px' }}
                disabled={!selectedFile || loading}
                size="large"
                variant="contained"
                onClick={handleSubmission}
              >
                Upload
              </Button>
              <MainOutLinedButton
                title="Create Pickup"
                onClick={() => {
                  navigate(`/app/pickup/newPickup`);
                }}
              />
            </BaseTableHeader>
          }
          headers={[
            'ID',
            'DATE',
            'NAME',
            'ADDRESS',
            'ITEMS',
            'CONSIGNMENTS',
            'STATUS',
            'COLLECTED',
            'UNCOLLECTED',
            'DRIVER',
            'ACTION',
          ]}
          data={pickupList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {pickupList?.map((pickup) => (
            <TableRow hover key={pickup._id}>
              <TableCell>{pickup._id}</TableCell>
              <TableCell>{moment(pickup.createdTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>{pickup.name}</TableCell>
              <TableCell>{`${pickup.address ?? ''} ${pickup.suburb ?? ''} ${pickup.state ?? ''} ${
                pickup.postcode ?? ''
              }`}</TableCell>
              <TableCell>{pickup.allConsignments.length ?? 1}</TableCell>
              <TableCell>{pickup.allConsignments?.join(', ')}</TableCell>
              <TableCell>{pickup.status}</TableCell>
              <TableCell>{pickup.collected?.length}</TableCell>
              <TableCell>{pickup.uncollected?.length}</TableCell>
              <TableCell>{driverList.find((item) => item._id === pickup.driverId)?.name}</TableCell>
              <TableCell>
                {/* <NoBorderButton
                  title={'EDIT'}
                  type={'primary'}
                  onClick={() => handleEditClick(pickup._id)}
                /> */}
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
