import React, { useState, useEffect } from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { Box, Button, Grid, TextField } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import RadioSelect from 'src/views/utils/RadioSelect';
import { SettingService } from 'src/services/setting';
import Select from 'src/views/utils/Select';
import { useParams } from 'react-router';
import { useNavigate } from 'react-router';

const CreateAdminView = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();
  const { adminId } = useParams();

  const [role, setRole] = useState('Admin');
  const [depotList, setDepotList] = useState([]);
  const [selectedDepot, setSelectedDepot] = useState();
  const [loading, setLoading] = useState(false);
  const [initialValues, setInitialValues] = useState({
    account: '',
    name: '',
    remark: '',
    password: '',
    confirmPassword: '',
  });

  useEffect(() => {
    const fetchDepotList = async () => {
      const depotRes = await SettingService.findDepotlist();
      setDepotList(depotRes.content);
    };

    const fetAdminAndDepot = async () => {
      const [adminRes, depotRes] = await Promise.all([
        SettingService.findAdminById(adminId),
        SettingService.findDepotlist(),
      ]);
      setDepotList(depotRes.content);
      setSelectedDepot(depotRes.content.find((item) => item._id === adminRes.content.depot?._id));
      setInitialValues({
        ...initialValues,
        name: adminRes.content.name,
        role: adminRes.content.role,
        account: adminRes.content.phoneNumber,
      });
    };

    const fetchData = async () => {
      try {
        adminId ? fetAdminAndDepot() : fetchDepotList();
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [adminId]);

  const createAdminRequest = async (value) => {
    try {
      const result = await SettingService.createAdmin({
        phoneNumber: value.account,
        name: value.name,
        email: value.email,
        password: value.password,
        role: role,
        depot: selectedDepot._id,
      });

      enqueueSnackbar(result.message, { variant: 'success' });
      navigate('/app/settings');
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const updateAdminRequest = async (value) => {
    try {
      const result = await SettingService.updateAdmin(adminId, {
        phoneNumber: value.account,
        name: value.name,
        email: value.email,
        role: role,
        depot: selectedDepot._id,
      });

      enqueueSnackbar(result.message, { variant: 'success' });
      navigate('/app/settings');
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const handleFormSubmit = (value) =>
    adminId ? updateAdminRequest(value) : createAdminRequest(value);

  const handleSelectChange = (e) => {
    e.preventDefault();
    setSelectedDepot(e.target.value);
  };

  const handleCancel = () => {
    navigate('/app/settings');
  };

  return (
    <Box>
      <GeneralPageTitleWithButton title={adminId ? 'EDIT ADMIN' : 'CREATE ADMIN'} />

      <Box mt={3}>
        <Grid container>
          {(!adminId || (adminId && initialValues.account)) && (
            <Grid item xs={12} md={6}>
              <Formik
                initialValues={initialValues}
                validationSchema={Yup.object().shape({
                  account: Yup.string().max(255).required('account is required'),
                  name: Yup.string().max(255).required('please input username'),
                  password: !adminId && Yup.string().max(255).required('please input password'),
                  confirmPassword:
                    !adminId &&
                    Yup.string()
                      .oneOf([Yup.ref('password')], 'password not match')
                      .required('please confirm password'),
                })}
                onSubmit={handleFormSubmit}
              >
                {({
                  errors,
                  handleBlur,
                  handleChange,
                  handleSubmit,
                  isSubmitting,
                  touched,
                  values,
                }) => (
                  <form onSubmit={handleSubmit}>
                    <TextField
                      error={Boolean(touched.account && errors.account)}
                      fullWidth
                      helperText={touched.account && errors.account}
                      label="phone/account"
                      margin="normal"
                      name="account"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.account}
                      variant="outlined"
                    />
                    <TextField
                      error={Boolean(touched.name && errors.name)}
                      fullWidth
                      helperText={touched.name && errors.name}
                      label="NAME"
                      margin="normal"
                      name="name"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.name}
                      variant="outlined"
                    />
                    <TextField
                      error={Boolean(touched.email && errors.email)}
                      fullWidth
                      helperText={touched.email && errors.email}
                      label="EMAIL"
                      margin="normal"
                      name="email"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.email}
                      variant="outlined"
                    />
                    {!adminId && (
                      <TextField
                        error={Boolean(touched.password && errors.password)}
                        fullWidth
                        helperText={touched.password && errors.password}
                        label="PASSWORD"
                        type={'password'}
                        margin="normal"
                        name="password"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.password}
                        variant="outlined"
                      />
                    )}
                    {!adminId && (
                      <TextField
                        error={Boolean(touched.confirmPassword && errors.confirmPassword)}
                        fullWidth
                        helperText={touched.confirmPassword && errors.confirmPassword}
                        type={'password'}
                        label="CONFIRM PASSWORD"
                        margin="normal"
                        name="confirmPassword"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.confirmPassword}
                        variant="outlined"
                      />
                    )}
                    <TextField
                      error={Boolean(touched.remark && errors.remark)}
                      fullWidth
                      helperText={touched.remark && errors.remark}
                      label="COMMENTS"
                      margin="normal"
                      name="remark"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.remark}
                      variant="outlined"
                    />
                    <Select
                      list={depotList}
                      values={selectedDepot}
                      handleChange={handleSelectChange}
                      option="title"
                      onBlur={handleBlur}
                      disabled={loading}
                      text="DEPOT"
                    />
                    <RadioSelect
                      label={'ROLE'}
                      radioName="role"
                      value={role}
                      radioLabelList={['SUPRE ADMIN(ALL PERMISSION)', 'ADMIN(MANAGEMENT)']}
                      radioValueList={['SuperAdmin', 'Admin']}
                      onChange={setRole}
                    />
                    <Box my={2}>
                      <Button
                        color="primary"
                        disabled={isSubmitting || loading}
                        fullWidth
                        size="large"
                        type="submit"
                        variant="contained"
                      >
                        {adminId ? 'SAVE' : 'CREATE'}
                      </Button>
                    </Box>
                  </form>
                )}
              </Formik>
              <Box my={2}>
                <Button
                  color="secondary"
                  disabled={loading}
                  fullWidth
                  size="large"
                  variant="contained"
                  onClick={handleCancel}
                >
                  Cancel
                </Button>
              </Box>
            </Grid>
          )}{' '}
        </Grid>
      </Box>
    </Box>
  );
};

export default CreateAdminView;
