import React from 'react';
import { Navigate } from 'react-router-dom';
import DashboardLayout from 'src/layouts/DashboardLayout';
import MainLayout from 'src/layouts/MainLayout';
import AccountView from 'src/views/account/AccountView';
import FreightsterManagementView from 'src/views/freightster';
import InboundView from 'src/views/inbound';
import LoginView from 'src/views/auth/LoginView';
import PasswordView from 'src/views/password';
import NotFoundView from 'src/views/errors/NotFoundView';
import RegisterView from 'src/views/auth/RegisterView';
import SettingsView from 'src/views/settings';
import ConsignmentView from 'src/views/consignment';
import ConsignmentForm from 'src/views/consignment/ConsignmentForm';
import DriverView from 'src/views/driver';
import DriverViewForm from 'src/views/driver/DriverForm';
import JobView from 'src/views/job';
import JobForm from 'src/views/job/CreateJob';
import PickUpManagementView from 'src/views/pickups';
import PostcodeForm from 'src/views/pickups/postcode/PostcodeForm';
import SupplierView from 'src/views/supplier';
import SupplierFormView from 'src/views/supplier/SupplierForm';
import TicketView from 'src/views/tickets';
import TicketForm from 'src/views/tickets/TicketForm';
import PrivateRoute from './PrivateRoute';
import PushView from 'src/views/pushes';

const routes = [
  {
    path: 'app',
    element: (
      <PrivateRoute>
        <DashboardLayout />
      </PrivateRoute>
    ),
    children: [
      { path: 'account', element: <AccountView /> },
      { path: 'freightster', element: <FreightsterManagementView /> },
      { path: 'freightster/:type', element: <FreightsterManagementView /> },
      { path: 'freightster/:type/:option', element: <FreightsterManagementView /> },
      {
        path: 'freightster/:type/:option/:currentPage',
        element: <FreightsterManagementView />,
      },
      { path: 'inbound', element: <InboundView /> },
      { path: 'inbound/:type', element: <InboundView /> },
      { path: 'dashboard', element: <Navigate to="/app/freightster/corssborder" /> },
      { path: 'settings', element: <SettingsView /> },
      { path: 'settings/:type', element: <SettingsView /> },
      { path: 'settings/:type/:settingType', element: <SettingsView /> },
      // { path: 'settings/accounts/edit/:adminId', element: <CreateAdminView /> },
      { path: 'settings/:type/:settingType/:adminId', element: <SettingsView /> },
      { path: 'consignment', element: <ConsignmentView /> },
      { path: 'consignment/list/:currentPage', element: <ConsignmentView /> },
      { path: 'consignment/edit/:consignmentId', element: <ConsignmentForm /> },
      { path: 'consignment/newConsignment', element: <ConsignmentForm /> },
      { path: 'job', element: <JobView /> },
      { path: 'job/list/:currentPage', element: <JobView /> },
      { path: 'job/edit/:jobId', element: <JobForm /> },
      { path: 'driver', element: <DriverView /> },
      { path: 'driver/list/:currentPage', element: <DriverView /> },
      { path: 'driver/edit/:driverId', element: <DriverViewForm /> },
      { path: 'driver/newDriver', element: <DriverViewForm /> },
      { path: 'pkmanagement', element: <PickUpManagementView /> },
      { path: 'pkmanagement/:type', element: <PickUpManagementView /> },
      { path: 'pkmanagement/:type/:option', element: <PickUpManagementView /> },
      {
        path: 'pkmanagement/:type/list/:currentPage',
        element: <PickUpManagementView />,
      },
      { path: 'pkmanagement/postcode/edit/:postcodeId', element: <PostcodeForm /> },
      { path: 'pkmanagement/postcode/newpostcode', element: <PostcodeForm /> },
      { path: 'supplier', element: <SupplierView /> },
      { path: 'supplier/list/:currentPage', element: <SupplierView /> },
      { path: 'supplier/edit/:supplierId', element: <SupplierFormView /> },
      { path: 'supplier/newSupplier', element: <SupplierFormView /> },
      { path: 'ticket', element: <TicketView /> },
      { path: 'ticket/list/:currentPage', element: <TicketView /> },
      { path: 'ticket/edit/:ticketId', element: <TicketForm /> },
      { path: 'ticket/newTicket', element: <TicketForm /> },
      { path: 'pushes', element: <Navigate to="/app/pushes/smspush" /> },
      { path: 'pushes/:type', element: <PushView /> },
      { path: 'pushes/:type/list/:currentPage', element: <PushView /> },
      { path: '*', element: <Navigate to="/404" /> },
    ],
  },
  {
    path: '/',
    element: <MainLayout />,
    children: [
      { path: 'login', element: <LoginView /> },
      { path: 'password', element: <PasswordView /> },
      { path: 'register', element: <RegisterView /> },
      { path: '404', element: <NotFoundView /> },
      { path: '/', element: <Navigate to="/app/freightster" /> },
      { path: '*', element: <Navigate to="/404" /> },
    ],
  },
];

export default routes;
