import { noneAuthPost } from './tools';

class AuthService {
  static loginWith = async ({ phone, password }) => {
    const res = await noneAuthPost('platform/accounts/login', {
      phoneNumber: phone,
      password: password,
    });

    return res.data;
  };

  static sendSMS = async ({ phoneNumber }) => {
    const params = { phoneNumber };
    const res = await noneAuthPost('platform/accounts/sendSMS', params);

    return res.data;
  };

  static forgetPassword = async ({ phoneNumber, verifyCode, newPassword }) => {
    const params = { phoneNumber, verifyCode, newPassword };
    const res = await noneAuthPost(`platform/accounts/forgetPassword`, params);

    return res.data;
  };
}

export default AuthService;
