import { saveAuthInfo } from 'src/utils/cookie';

export const SET_CURRENT_USER = 'SET_CURRENT_USER';

// Login - Get Cookie With Token
/**
 * 
 * @param {String} token 
 * @param {Object} userInfo 
 * 		"user": {
            "_id": "5fec0390056a803e4ed73ce6",
            "phoneNumber": 415361008,
            "name": "fyan",
            "createTime": "2020-12-30T04:35:28.869Z",
            "__v": 0,
            "role": "SuperAdmin"
        }
 */

export const setCurrentUser = authInfo => async dispatch => {
	// Save to cookie
	saveAuthInfo(authInfo);
	return dispatch({
		type: SET_CURRENT_USER,
		payload: authInfo,
	});
};
