import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import TicketService from 'src/services/ticket';
import GeneralTable from 'src/views/utils/GeneralTable';
import moment from 'moment';

// const orderList = [
// 	{ label: 'AZ', value: 'nameAZ' },
// 	{ label: 'ZA', value: 'nameZA' },
// 	{ label: '', value: 'TimeFarest' },
// 	{ label: '', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [ticketList, setTicketList] = useState(null);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setTicketList(null);
      setLoading(true);
      try {
        const result = await TicketService.getAllTicket({
          currentPage: currentPage,
          numberPerPage: limit,
        });
        setCount(result.content.numberOfResults);
        setTicketList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/ticket/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/ticket/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await TicketService.deleteTicket(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Ticket List" onRefresh={handleRefresh}>
              <MainOutLinedButton
                title="Create Ticket"
                onClick={() => {
                  navigate(`/app/ticket/newTicket`);
                }}
              />
            </BaseTableHeader>
          }
          headers={[
            'TICKET ID',
            'ADMIN',
            'DESCRIPTION',
            'START TIME',
            'LAST REPLY TIME',
            'STATUS',
            'ACTION',
          ]}
          data={ticketList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {ticketList?.map((ticket) => (
            <TableRow hover key={ticket._id}>
              <TableCell>{ticket._id}</TableCell>
              <TableCell>{ticket.admin}</TableCell>
              <TableCell>{ticket.description}</TableCell>
              <TableCell>{moment(ticket.startTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>{moment(ticket.lastreplayTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>{ticket.status ? 'CLOSE' : 'OPEN'}</TableCell>
              <TableCell>
                <NoBorderButton
                  title={'DETAIL'}
                  type={'primary'}
                  onClick={() => handleEditClick(ticket._id)}
                />
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
