import React from 'react';
import { Container } from '@material-ui/core';
import { useParams } from 'react-router';
import Invitation from './invitation';
import Results from './list';
// import Create from './create';
import View from './view';
import { generalStyles } from 'src/views/utils/GeneralStyle';
// import Edit from './edit';

const DomesticView = () => {
  const { option = 'list' } = useParams();
  const classes = generalStyles();

  const renderContent = () => {
    switch (option) {
      case 'list':
        return <Results />;

      case 'view':
        return <View />;

      // case 'edit':
      //   return <Edit />;

      case 'invitation':
        return <Invitation />;

      default:
        return <Results />;
    }
  };

  return (
    <Container maxWidth={false} className={classes.noLeftRightPadding}>
      {renderContent()}
    </Container>
  );
};

export default DomesticView;
