import { authGet, authPost, authDelete, authUpdate } from './tools';

class JobService {
  static getAllJob = async ({ currentPage, numberPerPage }) => {
    const params = {
      currentPage,
      numberPerPage,
    };

    const res = await authPost('platform/job/getJoblist', params);

    return res.data;
  };

  //Get a specific job.
  static findJobById = async (id) => {
    const payload = {
      jobId: id,
    };
    const res = await authPost(`platform/job/getjobdetail`, payload);
    return res.data;
  };
}

export default JobService;
