import React from 'react';
import { TableRow, TableCell, Button } from '@material-ui/core';
import { useNavigate } from 'react-router';
import moment from 'moment';

const Cell = ({ admin }) => {
  const navigate = useNavigate();
  const { _id, name, phoneNumber, createTime, lastLoginTime, depot, role } = admin;

  const goToEditPage = (id) => {
    navigate(`/app/settings/accounts/edit/${id}`);
  };
  return (
    <TableRow hover>
      <TableCell>{_id}</TableCell>
      <TableCell>{name}</TableCell>
      <TableCell>{phoneNumber}</TableCell>
      <TableCell>{moment(createTime).format('YYYY-MM-DD hh:mm:ss')}</TableCell>
      <TableCell>{moment(lastLoginTime).format('YYYY-MM-DD hh:mm:ss')}</TableCell>
      <TableCell>{depot?.title}</TableCell>
      <TableCell>{role}</TableCell>
      <TableCell>
        <Button
          onClick={() => {
            goToEditPage(_id);
          }}
        >
          EDIT
        </Button>
        {/* <Button className={classes.forbidButton}></Button> */}
      </TableCell>
    </TableRow>
  );
};

export default Cell;
