import { authGet, authPost, authDelete, authUpdate } from './tools';

class TicketService {
  static getAllTicket = async ({ currentPage, numberPerPage }) => {
    const params = {
      currentPage,
      numberPerPage,
    };

    const res = await authGet('platform/ticket/findTickets', params);

    return res.data;
  };

  //Get a specific ticket.
  static findTicketById = async (id) => {
    const res = await authGet(`platform/ticket/findTicketDetail/${id}`);
    return res.data;
  };

  //Create a ticket.
  static createTicket = async (params) => {
    const res = await authPost('platform/ticket/createTicket', params);
    return res.data;
  };

  //Create a ticket reply.
  static createTicketDetail = async (payload) => {
    const res = await authPost('platform/ticket/createTicketDetail', payload);
    return res.data;
  };

  //Update a ticket.
  static editTicket = async (ticketId) => {
    const payload = {
      status: true,
    };
    const res = await authUpdate(`platform/ticket/editTicket/${ticketId}`, payload);
    return res.data;
  };

  //Delete a named ticket.
  static deleteTicket = async (id) => {
    const res = await authDelete(`platform/ticket/${id}`);

    return res.data;
  };
}

export default TicketService;
