import { authGet } from './tools';

class PushService {
  static getAppPush = async ({ numberPerPage, currentPage }) => {
    const params = { numberPerPage, currentPage };
    const res = await authGet('/platform/pushes/appPush', params);

    return res.data;
  };

  static getSMSPush = async ({ numberPerPage, currentPage }) => {
    const params = { numberPerPage, currentPage };
    const res = await authGet('/platform/pushes/smsPush', params);

    return res.data;
  };
}

export default PushService;
