import React, { useEffect, useState } from 'react';
import { Box } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton from 'src/views/utils/Buttons';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import { SettingService } from 'src/services/setting';
import GeneralTable from 'src/views/utils/GeneralTable';
import Cell from './Cell';

const List = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();
  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [count, setCount] = useState(0);
  const [admins, setAdmins] = useState(null);

  useEffect(() => {
    const fetchAdmins = async () => {
      try {
        setAdmins(null);
        const result = await SettingService.fetchAdmins({
          currentPage: currentPage,
          numberPerPage: limit,
        });

        setAdmins(result.content.results);
        setCount(result.content.numberOfResults);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      }
    };
    fetchAdmins();
  }, [limit, currentPage, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/settings/accounts/list/${newPage + 1}`);
  };

  return (
    <>
      <GeneralPageTitleWithButton title="Accounts Management" />
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Admin List">
              <MainOutLinedButton
                title="Create Admin"
                onClick={() => {
                  navigate('/app/settings/accounts/create');
                }}
              />
            </BaseTableHeader>
          }
          headers={[
            'ID',
            'NAME',
            'PHONE',
            'CREATED AT',
            'LAST LOGIN',
            'FACILITY',
            'ROLE',
            'ACTION',
          ]}
          data={admins}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          limit={limit}
          count={count}
        >
          {admins?.map((admin) => (
            <Cell key={admin._id} admin={admin} />
          ))}
        </GeneralTable>
      </Box>
    </>
  );
};

export default List;
