import React, { useState } from 'react';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TablePagination,
  TextField,
  Button,
} from '@material-ui/core';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { BaseEmptyText, BaseLoaderText } from './BaseLoader';

/**
 *
 * @param {[String]} headers
 * @param {Array} data
 */
const GeneralTable = ({
  baseTableHeader = <div></div>,
  headers,
  data,
  handleLimitChange,
  handlePageChange,
  currentPage,
  limit,
  children,
  count,
  className,
  noTablePagination = false,
  loading = false,
}) => {
  const [page, setPage] = useState('');
  const positiveInt = new RegExp(/^[1-9]\d*$/);
  const pageChange = (e) => {
    if (!e.target?.value) {
      setPage('');
    } else {
      positiveInt.test(e.target.value) &&
        e.target.value <= Math.ceil(count / limit) &&
        setPage(e.target.value);
    }
  };

  const handleClick = (e) => {
    handlePageChange(e, page - 1 || 0);
    setPage('');
  };

  return (
    <Card className={className}>
      <PerfectScrollbar>
        <Box minWidth={850}>{baseTableHeader}</Box>
        {data ? (
          <Table>
            <TableHead>
              <TableRow>
                {headers.map((i) => (
                  <TableCell key={i}>{i}</TableCell>
                ))}
              </TableRow>
            </TableHead>

            <TableBody>{children}</TableBody>
          </Table>
        ) : (
          <BaseLoaderText />
        )}

        {data?.length === 0 && !loading && <BaseEmptyText />}
      </PerfectScrollbar>

      {!noTablePagination && (
        <div>
          <TablePagination
            component="div"
            count={count || 0}
            onChangePage={handlePageChange}
            onChangeRowsPerPage={handleLimitChange}
            page={currentPage || 0}
            rowsPerPage={limit}
            rowsPerPageOptions={[5, 10, 25]}
            style={{ display: 'inline-block', float: 'right' }}
          />
          <Button
            variant="contained"
            style={{ display: 'inline-block', float: 'right', marginTop: '8px' }}
            disabled={!page}
            onClick={handleClick}
          >
            Go
          </Button>
          <TextField
            variant="outlined"
            value={page}
            onChange={pageChange}
            size="small"
            label={`GoTo`}
            style={{ display: 'inline-block', float: 'right', width: '80px', marginTop: '5px' }}
          />
        </div>
      )}
    </Card>
  );
};

export default GeneralTable;
