import React, { useEffect, useState } from 'react';
import { Box, Button, Card, CardContent, Grid, makeStyles, TextField } from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import PostcodeService from 'src/services/postcode';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
}));

const CreateDelivery = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [initialValues, setInitialValues] = useState({
    postcode: '',
    sellerCount: '',
    driverId: '',
  });

  const { postcodeId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await PostcodeService.findPostcodeById(id);

        setInitialValues({
          postcode: result.content.postcode,
          sellerCount: result.content.sellerCount,
          driverId: result.content.driverId,
        });
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    postcodeId && fetchResults(postcodeId);
  }, [postcodeId, enqueueSnackbar]);

  const navigate = useNavigate();

  const requestWith = async (values, actions) => {
    try {
      await PostcodeService.createPostcode({
        postcode: values.postcode,
        sellerCount: values.sellerCount,
        driverId: values.driverId,
      });

      enqueueSnackbar('Create', { variant: 'success' });

      navigate('/app/postcode');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const updateDelivery = async (values, actions, id) => {
    try {
      await PostcodeService.editPostcode(postcodeId, values);

      enqueueSnackbar('', { variant: 'success' });

      navigate('/app/pkmanagement/postcode');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const handleFormSubmit = (values, actions) => {
    !postcodeId ? requestWith(values, actions) : updateDelivery(values, actions, postcodeId);
  };

  const handleCancel = () => {
    navigate(`/app/pkmanagement/postcode`);
  };

  const renderContent = () => (
    <Grid container>
      <Grid item container xs={12}>
        {(!postcodeId || (postcodeId && initialValues.postcode)) && (
          <Formik
            initialValues={initialValues}
            validationSchema={Yup.object().shape({
              postcode: Yup.string().max(255).required('*required'),
              sellerCount: Yup.string().max(255).required('*required'),
            })}
            onSubmit={(values, actions) => {
              handleFormSubmit(values, actions);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit} className={classes.form}>
                <Grid item container xs={12} spacing={2}>
                  <Grid item xs={12}>
                    <h3>Postcode Information</h3>
                  </Grid>
                  <Grid item xs={4}>
                    <TextField
                      error={Boolean(touched.postcode && errors.postcode)}
                      fullWidth
                      helperText={touched.postcode && errors.postcode}
                      label="Postcode*"
                      margin="normal"
                      name="postcode"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values?.postcode}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.sellerCount && errors.sellerCount)}
                      fullWidth
                      helperText={touched.sellerCount && errors.sellerCount}
                      label="Count of Seller*"
                      margin="normal"
                      name="sellerCount"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.sellerCount}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                  <Grid item xs={5}>
                    <TextField
                      error={Boolean(touched.driverId && errors.driverId)}
                      fullWidth
                      helperText={touched.driverId && errors.driverId}
                      label="Driver Id"
                      margin="normal"
                      name="driverId"
                      onBlur={handleBlur}
                      multiline
                      onChange={handleChange}
                      value={values?.driverId}
                      variant="outlined"
                      disabled={isSubmitting}
                    />
                  </Grid>
                </Grid>

                <Box my={2} mx={'auto'} className={classes.buttons}>
                  <Button
                    color="primary"
                    disabled={isSubmitting || loading}
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    {postcodeId ? 'Save' : 'Create'}
                  </Button>
                  <Button
                    disabled={isSubmitting || loading}
                    size="large"
                    variant="contained"
                    type="reset"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        )}
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={postcodeId ? 'Edit Postcode' : 'Create Postcode'} />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateDelivery;
