import React from 'react';
import { Toolbar, Typography, Box, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import MainOutLinedButton from './Buttons';

const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
}));

const BaseTableHeader = ({ title, onRefresh, children }) => {
  const classes = useToolbarStyles();

  return (
    <Toolbar className={classes.root}>
      <Grid
        container
        direction="row"
        justify="space-between"
        alignContent="flex-end"
        alignItems="center"
      >
        <Grid item container xs={6} alignItems="center">
          <Grid item>
            <Typography className={classes.title} variant="h3" id="tableTitle" component="div">
              {title}
            </Typography>
          </Grid>
          {onRefresh && (
            <Grid item>
              <Box className={classes.root}>
                <MainOutLinedButton title="Refresh" onClick={onRefresh} />
              </Box>
            </Grid>
          )}
        </Grid>
        <Grid item container xs={6} direction="row" alignItems="center" justify="flex-end">
          {React.Children.toArray(children).map((i, index) => (
            <Box key={index} className={classes.root}>
              {i}
            </Box>
          ))}
        </Grid>
      </Grid>
    </Toolbar>
  );
};

export default BaseTableHeader;
