import React from 'react';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import theme from 'src/theme';
import { FormLabel, Box, Grid } from '@material-ui/core';

/**
 * @param {[String]} labels:
 * @param {[String]} values:
 * @param {Bool} value
 * @param {Function(Bool)} onSelect:
 */
const ToggleButtons = ({
  labels,
  values = [true, false],
  name,
  value,
  desc,
  onSelect,
  children,
  disabled = false,
}) => {
  return (
    <Grid container alignItems="center">
      <Grid item>
        <ToggleButtonGroup
          value={value}
          exclusive
          onChange={(e, value) => {
            if (value !== null) {
              onSelect({
                target: {
                  name: name,
                  value: value,
                },
              });
            }
          }}
          aria-label="text alignment"
          style={{
            height: 40,
          }}
        >
          {labels.map((label, index) => (
            <ToggleButton
              disabled={disabled}
              style={{
                color: value === values[index] ? '#fff' : null,
                backgroundColor: value === values[index] ? theme.palette.primary.light : null,
              }}
              key={index}
              value={values[index]}
            >
              {label}
            </ToggleButton>
          ))}
        </ToggleButtonGroup>
      </Grid>
      <Grid item>
        <Box ml={1} mr={1}>
          {children}
        </Box>
        <Box ml={1} mr={3}>
          <FormLabel>{desc}</FormLabel>
        </Box>
      </Grid>
    </Grid>
  );
};

export default ToggleButtons;
