import { authGet, authPost, authDelete, authUpdate } from './tools';

class PostcodeService {
  static getAllPostcode = async ({ currentPage, numberPerPage }) => {
    const params = {
      currentPage,
      numberPerPage,
    };

    const res = await authGet('platform/postcode/findPostcodes', params);

    return res.data;
  };

  //Get a specific postcode.
  static findPostcodeById = async (id) => {
    const res = await authGet(`platform/postcode/findPostcodeById/${id}`);
    return res.data;
  };

  //Create a postcode.
  static createPostcode = async (params) => {
    const res = await authPost('platform/postcode/createPostcode', params);
    return res.data;
  };

  //Update a postcode.
  static editPostcode = async (postcodeId, payload) => {
    const res = await authUpdate(`platform/postcode/editPostcode/${postcodeId}`, payload);
    return res.data;
  };

  //Delete a named postcode.
  static deletePostcode = async (id) => {
    const res = await authDelete(`platform/postcode/${id}`);

    return res.data;
  };
}

export default PostcodeService;
