import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import PostcodeService from 'src/services/postcode';
import GeneralTable from 'src/views/utils/GeneralTable';
import moment from 'moment';

// const orderList = [
// 	{ label: 'AZ', value: 'nameAZ' },
// 	{ label: 'ZA', value: 'nameZA' },
// 	{ label: '', value: 'TimeFarest' },
// 	{ label: '', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [postcodeList, setPostcodeList] = useState(null);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setPostcodeList(null);
      setLoading(true);
      try {
        const result = await PostcodeService.getAllPostcode({
          currentPage: currentPage,
          numberPerPage: limit,
        });
        setCount(result.content.numberOfResults);
        setPostcodeList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/pkmanagement/postcode/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/pkmanagement/postcode/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await PostcodeService.deletePostcode(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Postcode List" onRefresh={handleRefresh}>
              <MainOutLinedButton
                title="Create Postcode"
                onClick={() => {
                  navigate(`/app/pkmanagement/postcode/newPostcode`);
                }}
              />
            </BaseTableHeader>
          }
          headers={['POSTCODE', 'COUNT OF SELLER', 'DRIVER', 'UPDATE', 'ACTION']}
          data={postcodeList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {postcodeList?.map((postcode) => (
            <TableRow hover key={postcode._id}>
              <TableCell>
                <Box alignItems="center" display="flex">
                  <Typography color="textPrimary" variant="body1">
                    {postcode.postcode}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>{postcode.sellerCount}</TableCell>
              <TableCell>{postcode.driverId}</TableCell>
              <TableCell>{moment(postcode.createTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>
                <NoBorderButton
                  title={'EDIT'}
                  type={'primary'}
                  onClick={() => handleEditClick(postcode._id)}
                />
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
