import { authGet, authPost, authUpdate } from './tools';

class InboundService {
  static inboundcheck = async (consignmentNumber) => {
    const payload = {
      consignmentNumber,
    };
    const res = await authPost('platform/inbound/inboundcheck', payload);

    return res.data;
  };

  static inboundConfirm = async (consignmentNumber) => {
    const payload = {
      consignmentNumber,
    };
    const res = await authPost('platform/inbound/inboundConfirm', payload);

    return res.data;
  };
}

export default InboundService;
