import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Box, makeStyles, Grid } from '@material-ui/core';
import DateRangePicker from 'src/views/utils/DateRangePicker';
import { SearchTextField } from 'src/views/utils/TextFields';
import GeneralSearchView from 'src/views/utils/GeneralSearchView';

const useStyles = makeStyles((theme) => ({
  root: {},
}));

const Toolbar = ({
  className,
  title,
  onSearch,
  onRefresh,
  disabled = false,
  placeholder = 'Numbers',
  hideSearchText = false,
  ...rest
}) => {
  const classes = useStyles();

  const [text, setText] = useState('');
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const onSearchClicked = () => {
    onSearch(text, startDate, endDate);
  };

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <GeneralSearchView onSearch={onSearchClicked} disabled={disabled}>
        <Box maxWidth={350}>
          {!hideSearchText && (
            <SearchTextField placeholder={placeholder} onChange={setText} disabled={disabled} />
          )}
        </Box>
        <Grid item>
          <DateRangePicker
            onStartChange={setStartDate}
            onEndChange={setEndDate}
            disabled={disabled}
            leadingLabel="Time From"
            trailingLabel="To"
          />
        </Grid>
      </GeneralSearchView>
    </div>
  );
};

Toolbar.propTypes = {
  className: PropTypes.string,
};

export default Toolbar;
