import React from 'react';
import {
  Box,
  FormControl,
  FormLabel,
  FormControlLabel,
  RadioGroup,
  Radio,
} from '@material-ui/core';

/**
 *
 * @param {[String]} radioLabelList
 * @param {[String]} radioValueList
 */
const RadioSelect = ({ label, radioName, value, radioLabelList, radioValueList, onChange }) => {
  return (
    <Box mt={2} mb={2}>
      <FormControl component="fieldset">
        <FormLabel component="legend">{label}</FormLabel>
        <Box mt={1} mb={1}>
          <RadioGroup
            aria-label={radioName}
            name={radioName}
            value={value}
            onChange={(e) => {
              onChange(e.target.value);
            }}
          >
            {radioLabelList.map((i, index) => (
              <FormControlLabel
                key={index}
                value={radioValueList[index]}
                control={<Radio />}
                label={i}
                labelPlacement="end"
              />
            ))}
          </RadioGroup>
        </Box>
      </FormControl>
    </Box>
  );
};

export default RadioSelect;
