import React from 'react';
import { FormControl, InputLabel, Select as MaterialSelect, MenuItem } from '@material-ui/core';

//Obejct.option is value and Object.show is text present in select.
const Select = ({
  list,
  values,
  handleChange,
  option,
  label,
  name,
  disabled,
  text = '',
}) => {
  return (
    <FormControl size="medium" variant="outlined" margin="normal" fullWidth disabled={disabled}>
      <InputLabel>{text}</InputLabel>
      <MaterialSelect
        fullWidth
        value={values ? values : ''}
        onChange={handleChange}
        label={label || 'label'}
        name={name || 'name'}
      >
        {list &&
          list.length > 0 &&
          list.map((item, index) => (
            <MenuItem key={index} value={item}>
              {item[option] || ''}
            </MenuItem>
          ))}
      </MaterialSelect>
    </FormControl>
  );
};

export default Select;
