import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, Grid, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import SupplierService from 'src/services/supplier';
import GeneralTable from 'src/views/utils/GeneralTable';

// const orderList = [
// 	{ label: 'AZ', value: 'nameAZ' },
// 	{ label: 'ZA', value: 'nameZA' },
// 	{ label: '', value: 'TimeFarest' },
// 	{ label: '', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [supplierList, setSupplierList] = useState(null);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setSupplierList(null);
      setLoading(true);
      try {
        const result = await SupplierService.getAllSupplier({
          currentPage: currentPage,
          numberPerPage: limit,
        });
        setCount(result.content.numberOfResults);
        setSupplierList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/supplier/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/supplier/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await SupplierService.deleteSupplier(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Supplier List" onRefresh={handleRefresh}>
              {/* <MainOutLinedButton
                title="Create Supplier"
                onClick={() => {
                  navigate(`/app/supplier/newSupplier`);
                }}
              /> */}
            </BaseTableHeader>
          }
          headers={[
            'ID',
            'DRIVER ID',
            'USERNAME',
            'COMPANY',
            'PHONE',
            'EMAIL',
            'FACILITY',
            'ACTION',
          ]}
          data={supplierList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {supplierList?.map((supplier) => (
            <TableRow hover key={supplier._id}>
              <TableCell>
                <Box alignItems="center" display="flex">
                  <Typography color="textPrimary" variant="body1">
                    {supplier._id}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>{supplier.supplierId}</TableCell>
              <TableCell>{supplier.name}</TableCell>
              <TableCell>{supplier.company}</TableCell>
              <TableCell>{supplier.phone}</TableCell>
              <TableCell>{supplier.email}</TableCell>
              <TableCell>{supplier.facility}</TableCell>
              <TableCell>
                <Grid container spacing={1}>
                  <Grid item>
                    {/* <NoBorderButton
                      title="EDIT"
                      type="primary"
                      onClick={() => handleEditClick(supplier._id)}
                    /> */}
                  </Grid>
                  {/* TODO: BAN Supplier, Select options */}
                  {/* <Grid item>
                    <NoBorderButton
                      title="BAN"
                      type="secondary"
                      onClick={() => handleEditClick(supplier._id)}
                    />
                  </Grid> */}
                </Grid>
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
