import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, TableCell, TableRow, Typography } from '@material-ui/core';
import moment from 'moment';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import JobService from 'src/services/job';
import GeneralTable from 'src/views/utils/GeneralTable';

// const orderList = [
// 	{ label: 'AZ', value: 'nameAZ' },
// 	{ label: 'ZA', value: 'nameZA' },
// 	{ label: '', value: 'TimeFarest' },
// 	{ label: '', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(10);
  const [jobList, setJobList] = useState(null);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setJobList(null);
      setLoading(true);
      try {
        const result = await JobService.getAllJob({
          currentPage: currentPage,
          numberPerPage: limit,
        });
        setCount(result.content.numberOfResults);
        setJobList(result.content.results);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/job/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/job/edit/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await JobService.deleteJob(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Job List" onRefresh={handleRefresh}>
              <MainOutLinedButton
                title="Create Job"
                onClick={() => {
                  navigate(`/app/job/newJob`);
                }}
              />
            </BaseTableHeader>
          }
          headers={[
            'ID',
            'DRIVER',
            'EMAIL',
            'FACILITY',
            'STATUS',
            'TOTAL',
            'DELIVERED',
            'FINALISED',
            'UPDATE TIME',
            'PAID',
            'ACTION',
          ]}
          data={jobList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {jobList?.map((job) => (
            <TableRow hover key={job._id}>
              <TableCell>
                <Box alignItems="center" display="flex">
                  <Typography color="textPrimary" variant="body1">
                    {job._id}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>{job.driverId}</TableCell>
              <TableCell>{job.email}</TableCell>
              <TableCell>{job.destination}</TableCell>
              <TableCell>{job.status}</TableCell>
              <TableCell>{job.total}</TableCell>
              <TableCell>{job.delivered}</TableCell>
              <TableCell>{job.finalised}</TableCell>
              <TableCell>{moment(job.createdTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
              <TableCell>{job.paid ? 'Y' : 'N'}</TableCell>
              <TableCell>
                <NoBorderButton
                  title={'DETAIL'}
                  type={'primary'}
                  onClick={() => handleEditClick(job._id)}
                />
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
