import { authGet, authPost, authDelete, authUpdate } from './tools';

class DriverService {
  static getAllDriver = async ({ currentPage, numberPerPage }) => {
    const params = {
      currentPage,
      numberPerPage,
    };

    const res = await authGet('platform/driver/findDrivers', params);

    return res.data;
  };

  //Get a specific driver.
  static findDriverById = async (id) => {
    const res = await authGet(`platform/driver/findDriverById/${id}`);
    return res.data;
  };

  //Create a driver.
  static createDriver = async (params) => {
    const res = await authPost('platform/driver/createDriver', params);
    return res.data;
  };

  //Update a driver.
  static editDriver = async (driverId, payload) => {
    const res = await authUpdate(`platform/driver/editDriver/${driverId}`, payload);
    return res.data;
  };

  //Delete a named driver.
  static deleteDriver = async (id) => {
    const res = await authDelete(`platform/driver/${id}`);

    return res.data;
  };
}

export default DriverService;
